<template>
  <div class="layout">
    <div class="header">
      <div :class="'title' + ' ' + controlColor"><a href="#"><i class="material-icons arrow">keyboard_arrow_left</i></a>{{charpterNumber}} {{charpterTitle}}</div>
    </div>
    <div :class="'content' + ' ' + controlColor">
      <div class="showBottom" style="width: 100%; height: 80%; background-color: #AAA; position: fixed; left: 0; top: 10%; opacity:0" @click="openBottomSheet"></div>
      <div v-for="charpter in charpterAll">
        <h4 class="charpterTitle">{{charpter.number}} {{charpter.title}}</h4>
        <div :style="'fontSize:' + fontVal + 'px'">
          <p v-for="charpterContent in charpter.content">{{charpterContent}}</p>
          <!-- <mu-infinite-scroll :scroller="scroller" :loading="loading" @load="loadMore"/> -->
        </div>
        
      </div>
      <button class="nextCharpter" @click="nextCharpter()" :disabled="nextButton"></button>
      <div>
        <mu-drawer right :open="catalogOpen" @close="catalogToggle()" :docked="false" width="80%">
          <mu-appbar title=" "/>
          <mu-list>
            <mu-list-item v-for="directory in directorys" :title="'' + directory.charpterNumber + '  ' + directory.charpterTitle" @click="changeCharpter(directory.charpterNumber)" :titleClass="{'charpterListStyle': directory.chapterType}"/>
          </mu-list>
        </mu-drawer>
      </div>
    </div>

    <div class="footer">
      <mu-bottom-sheet :open="bottomSheet" @close="closeBottomSheet">
        <div :style="showSet">
          <div class="fontSizes"><span @click="fontVal = fontVal-1">A -</span> <span @click="fontVal = fontVal+1;">A +</span></div>
          <mu-slider v-model="fontVal" :step="1" class="demo-slider" :max="22" :min="12" style="display: none"/>
          
          <div class="adjustColor">
            <button class="demo-raised-button adjustColBUto" style="background-color: rgb(222,206,169)" @click="changeColor('oneColor')"/>
            <button class="demo-raised-button adjustColBUto" style="background-color: rgb(202,217,232); width=20%;" @click="changeColor('twoColor')"/>
            <button class="demo-raised-button adjustColBUto" style="background-color: rgb(209,237,209)" @click="changeColor('threeColor')"/>
            <button class="demo-raised-button adjustColBUto" style="background-color: rgb(230,230,230)" @click="changeColor('fourColor')"/>
          </div>
        </div>
        <div :style="showPro">
          
          <span class="fontReduce">: {{progressVal}}%</span>
          <div class="demo-slider">
            <mu-slider v-model="charpterNumber" :step="1" :min="1" :max="allCharpterNumber" @input="sliderChange(charpterNumber)"/>
          </div>

          <div><span class="frontChar allChangeChar" @click="changeCharpter(charpterNumber-1)"></span> <span class="nextChar allChangeChar" @click="changeCharpter(charpterNumber+1)"></span></div>
        </div>
        <mu-bottom-nav :value="bottomNav" @change="handleChange">
          <mu-bottom-nav-item value="catalog" title="" icon="format_list_bulleted" class="bottomNavIcon"/>
          <mu-bottom-nav-item value="progrss" title="" icon="trending_flat" class="bottomNavIcon"/>
          <mu-bottom-nav-item value="setting" title="" icon="text_format" class="bottomNavIcon"/>
          <mu-bottom-nav-item value="day" title="" icon="wb_sunny" class="bottomNavIcon" :style="'display:'+dayStyle"/>
          <mu-bottom-nav-item value="night" title="" icon="timelapse" class="bottomNavIcon" :style="'display:'+nightStyle"/>
        </mu-bottom-nav>
      </mu-bottom-sheet>
    </div>
    <mu-dialog :open="purchaseCharper" title="" @close="purchaseCharper = false">
      ?
      <mu-flat-button slot="actions" @click="purchaseCharper = false" primary label=""/>
      <mu-flat-button slot="actions" primary @click="makeSurePurchase()" label=""/>
    </mu-dialog>
  </div>
</template>

<script>
export default {
  mounted() {
    this.scroller = this.$el
    if(this.storageGetter('fontVal')){
      this.fontVal = this.storageGetter('fontVal')
    }
    if(this.storageGetter('controlColor')){
      this.controlColor = this.storageGetter('controlColor')
    }
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      loading: false,
      scroller: null,
      bottomSheet: false,
      bottomNav: '',
      catalogOpen: false,
      progressVal: '',
      fontVal: 16,
      allCharpterNumber: 5,
      //
      charpterNumber: 1,
      //
      showSet: 'display:none;',
      showPro: 'display:none',
      controlColor: 'oneColor',
      controlColorChange: 'oneColor',
      //
      charpterTitle: '',
      charpterAll: [],
      bookName: '',
      charpterContents: ["", "vue"],
      bookId: '',
      //Id
      directorys: [{
        charpterNumber: 10,
        charpterTitle: 'vue',
        bookId: '10' ,
        chapterType: 0
      }],
      nextButton: false,
      dayStyle: 'none',
      nightStyle: 'block',
      chapterType: 0,
      //01
      purchaseCharper: false
      //
    }
  },
  mounted: function(){
    this.$ajax({
      method: 'get',
      url: './static/mock/reading_dev1.json',
      data: {
        
      }
    })
    .then((response) => {
      response = response.data
      this.cahngePage(response)
    })
    this.scroller = this.$el
  },
  watch: {
    fontVal: function(val){
      this.storageSetter('fontVal', val)
    },
    charpterNumber: function(){
      //
      this.progressVal = (this.charpterNumber / this.allCharpterNumber) * 100
    }
  },
  methods: {
    isLogin(){
      //(1)
      var isLogin = false
      this.$ajax({
        method: 'get',
        url: '...',
        params: {

        }
      })
      .then((response) => {
        response = response.data
        if(response){
          //
          isLogin = true
        }else{
          //
          isLogin = false
        }
      })
      return isLogin
    },
    isRead(){
      //(2)
      var isRead = false
      this.$ajax({
        method: 'get',
        url: urls,
        params: {
          //IdId
          bookId: this.bookId,
          charpterNumber: this.charpterNumber
        }
      })
      .then((response) => {
        response = response.data
        if(response){
          //
          isRead = true
        }else{
          //
          isRead = false
        }
      })
      return isRead
    },
    nowAfford(){
      //(4)
      var ifAfford = false
      this.$ajax({
        method: 'get',
        url: urls,
        params: {
          //IdId
          bookId: this.bookId,
          charpterNumber: this.charpterNumber
        }
      })
      .then((response) => {
        response = response.data
        if(response){
          //
          isRead = true
        }else{
          //
          isRead = false
        }
      })
      return ifAfford
    },
    makeSurePurchase(){
      //
      this.purchaseCharper = false
      this.$router.push('/pay');    //
      if(this.nowAfford){
        //
        this.requstCharpter(this.charpterNumber)
      }else{
        //,
        this.$router.push('/pay')


      }
    },
    payCharpterHandle(val){
      //,
      //val
      if(this.isLogin()){
        //
        if(this.isRead()){
          //
          this.requstCharpter(val)
        }else{
          //,
          this.purchaseCharper = true
          //makeSurePurchase
        }
      }else{
        //
        //this.$router.push('/login');


      }
    },
    changeCharpter(val) {
      //
      
        if(val > 0 && val < (this.allCharpterNumber + 1)){
          if(!this.directorys[val - 1].chapterType){
            //
            console.log(val)
            this.charpterNumber = val
            this.charpterAll = []
            this.requstCharpter(val)
          }else{
            //
            this.purchaseCharper = true     //
            this.payCharpterHandle(val)
          }

        }
    },
    sliderChange(val) {
      //
      console.log(val)
      this.changeCharpter(val)
    },
    cahngePage(response){
      //
      this.allCharpterNumber = response.allCharpterNumber
      this.charpterNumber = response.charpterNumber
      this.chapterType = response.chapterType
      this.charpterContents = response.charpterContents
      this.charpterTitle = response.charpterTitle
      this.bookId = response.bookId
      this.directorys = response.directorys
      this.charpterAll.push({title: this.charpterTitle, content: this.charpterContents, number: this.charpterNumber})
    },
    nextCharpter(){
      //
      if(this.charpterNumber < this.allCharpterNumber){
        if(!this.directorys[this.charpterNumber].chapterType){
          //
          this.charpterNumber = this.charpterNumber + 1
          this.nextButton = true
          this.requstCharpter(this.charpterNumber)
        }else{
          //
          this.purchaseCharper = true     //
          this.payCharpterHandle(this.charpterNumber+1)
        }
        
      }else{
        alert('')
      }
    },
    requstCharpter(val){
      //(3)
      let urls = './static/mock/reading_dev'
      urls = urls + val + '.json'
      this.$ajax({
        method: 'get',
        url: urls,
        data: {
          
        }
      })
      .then((response) => {
        response = response.data
        this.cahngePage(response)
        this.nextButton = false
      })
    },
    closeBottomSheet () {
      //
      this.bottomSheet = false
      this.bottomNav = ''
      this.showPro = 'display:none'
      this.showSet = 'display:none'
    },
    openBottomSheet () {
      //
      this.bottomSheet = true
      this.progressVal = (this.charpterNumber / this.allCharpterNumber) * 100
    },
    handleChange (val) {
      //
      this.bottomNav = val
      if(val == "catalog"){
        this.catalogOpen = true
        this.bottomSheet = false
        this.showPro = 'display:none'
        this.showSet = 'display:none'
      }
      if(val == "progrss"){
        //
        this.showPro = 'display:block'
        this.showSet = 'display:none'
      }
      if(val == "setting"){
        //
        this.showSet = 'display:block'
        this.showPro = 'display:none'
      }
      if(val == "day"){
        this.controlColor = this.controlColorChange
        this.storageSetter('controlColor', this.controlColor)
        this.dayStyle = 'none'
        this.nightStyle = 'block'
      }
      if(val == "night"){
        this.controlColor = 'nightStyle'
        this.storageSetter('controlColor', this.controlColor)
        this.dayStyle = 'block'
        this.nightStyle = 'none'
      }
    },
    catalogToggle () {
      //
      this.catalogOpen = !this.catalogOpen
      this.bottomNav = ''
    },
    changeColor(val) {
      //
      this.controlColor = val,
      this.controlColorChange = val
      this.storageSetter('controlColor', val)
    },
    storageGetter(key){
      //
      const prefix = 'html5_reader_'
      return localStorage.getItem(prefix + key);
    },
    storageSetter(key, val){
      //
      const prefix = 'html5_reader_'
      localStorage.setItem(prefix + key, val)
    }
  }
}
</script>
<style>
  html{
    font-size: 16px;
  }
  .mu-item-content{
    text-align: left;
    position: absolute;
  }
  .arrow{
    float: left;
    line-height: 40px;
    padding: 0 10px;
    color: rgba(0,0,0,.4);
  }
  .title{
    width: 100%;
    height: 40px;
    line-height: 40px;
    color: rgba(0,0,0,.4);
    text-align: left;
    position: fixed;
    font-size: .85em;
    top: 0; 
  }
  .content{
    padding: 16px;
  }
  .charpterTitle{
    padding: 56px 0 16px 0;
    font-size: 1.4em;
    text-align: left;
  }
  p{
    text-align: left;
    text-indent: 2em;
    margin: 10px 0;
    /*font-size: 18px;*/
  }
  .demo-slider{
    width: 80%;
    margin: 0 auto;
  }
  .fontReduce{
    width: 100%;
    text-align: center;
    display: inline-block;
    font-size: 18px;
    padding-top: 20px;
  }
  .nextCharpter{
    width: 80%;
    height: 40px;
    border-radius: 30px;
    background-color: #ed424b;
    color: #FFF;
    box-shadow: none;
    border: none;
    text-align: center;
    line-height: 40px;
    margin-top: 10px;
  }
  .adjustColor{
    text-align: center;
    margin: 20px 0 30px 0;
  }
  .adjustColBUto{
    width: 20%;
    height: 34px;
    margin: 0 2px;
    border: none;
    border-radius: 6px;
  },
  .charptersContent{
    height: 100px;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
  }
  .showBottom{
    width: 100%;
    height: 4000px;
    margin: 0 auto;
    background-color: #CCC;
    display: block;
  }
  .oneColor{
    background-color: rgb(222,206,169);
    color: rgba(0,0,0,.85);
  }
  .twoColor{
    background-color: rgb(202,217,232);
    color: rgba(0,0,0,.85);
  }
  .threeColor{
    background-color: rgb(209,237,209);
    color: rgba(0,0,0,.85);
  }
  .fourColor{
    background-color: rgb(230,230,230);
    color: rgba(0,0,0,.85);
  }
  .nightStyle{
    background-color: #1a1a1a;
    color: rgba(255,255,255,.5);
  }
  .fontSizes{
    text-align: center;
    font-size: 18px;
    margin-top: 20px;
  }
  .fontSizes span{
    display: inline-block;
    font-size: 20px;
    line-height: 36px;
    border: 1px solid #BBB;
    width: 40%;
    height: 36px;
    border-radius: 8px;  
    background-color: #BBB;
    color: #FFF;
    margin-left: 6px;
    text-align: center;
  }
  .allChangeChar{
    width: 20%;
    height: 32px;
    line-height: 32px;
    display: inline-block;
    text-align: center;
    background-color: #CCC;
    color: #FFF;
    border-radius: 10px;
    margin-top: 5px;
    margin-bottom: 5px;
  }
  .frontChar{
    margin-left: 10%;
  }
  .nextChar{
    margin-left: 40%;
  }
  .charpterListStyle{
    color: #AAA;
  }
</style>


