<template>
<div>
  <mu-select-field v-model="game1" :labelFocusClass="['label-foucs']" label="">
    <mu-menu-item v-for="text,index in list" :key="index" :value="index" :title="text" />
  </mu-select-field>
  <mu-flat-button primary @click="testType = 1" label=""/>
  <mu-flat-button primary @click="testType = 0" label=""/>
  <mu-table :fixedFooter="fixedFooter" :fixedHeader="fixedHeader" :height="height"
    :enableSelectAll="enableSelectAll" :multiSelectable="multiSelectable"
    :selectable="selectable" :showCheckbox="showCheckbox">
    <mu-thead slot="header">
      <mu-tr>
        <mu-th tooltip=""></mu-th>
        <mu-th></mu-th>
        <mu-th></mu-th>
      </mu-tr>
    </mu-thead>
    <mu-tbody>
      <mu-tr v-for="item,index in tableData"  :key="index" :selected="item.selected">
        <mu-td>{{item.code}}</mu-td>
        <mu-td>
          <mu-flat-button primary @click="showProInfo(index)" label=""/>
        </mu-td>
        <mu-td>
          <mu-flat-button primary @click="deleteProInfo(testType,index)" label=""/>
        </mu-td>
      </mu-tr>
    </mu-tbody>
  </mu-table>
  <mu-dialog :open="showSelect" title="" @close="showSelect = false">
    <p> {{ selectInfo.question }}</p>
    <p>A: {{ selectInfo.cho_a }}</p>
    <p>B: {{ selectInfo.cho_b }}</p>
    <p>C: {{ selectInfo.cho_c }}</p>
    <p>D: {{ selectInfo.cho_d }}</p>
    <p> {{ selectInfo.anwser }}</p>
    <mu-flat-button slot="actions" @click="showSelect = false" primary label=""/>
    <mu-flat-button slot="actions" primary @click="showSelect = false" label=""/>
  </mu-dialog>
  <mu-dialog :open="showJudge" title="" @close="showJudge = false">
    <p>: {{ judgeInfo.question }}</p>
    <p> {{ judgeInfo.anwser }}</p>
    <mu-flat-button slot="actions" @click="showJudge = false " primary label=""/>
    <mu-flat-button slot="actions" primary @click="showJudge = false" label=""/>
  </mu-dialog>
</div>
</template>

<script>
export default {
  data () {
    return {
      game1: 0,
      list: ['', '', '', '', '', ''],
      tableData: [
        {
          code: '1505120206'
        }
      ],
      fixedHeader: true,
      fixedFooter: false,
      selectable: false,
      multiSelectable: false,
      enableSelectAll: false,
      showCheckbox: false,
      height: '500px',
      value: '1',
      testType: 1,
      nowTable: [],
      showSelect: false,
      showJudge: false,
      selectInfo: {
        question: '',
        cho_a: '',
        cho_b: '',
        cho_c: '',
        cho_d: '',
        anwser: ''
      },
      judgeInfo: {
        question: '',
        anwser: ''
      }
    }
  },
 mounted: function(){
    this.loadProInfo(this.game1, this.testType, -1)
  },
  watch: {
    testType: function(val){
      this.loadProInfo(this.game1, val, -1)
    },
    game1: function(val){
      this.loadProInfo(val, this.testType, -1)
    }
  },
  methods: {
    loadProInfo(proIndex, testType, index){
      //,
      //
      //index-1index()
      if(testType){
        //
        this.$ajax({
        method: 'post',
        url: 'http://localhost:3000/api/getSelectInfo',
        data: {
          sub_no: proIndex + 2,
          cho_no: index
        }
        })
        .then((response) => {
          console.log(response.data.tableData)
          response = response.data.tableData 
          if(index < 0){
            this.changeInfoInTable(response, 1)
          }else{
            this.selectInfo = response[0]
            //
            this.showSelect = true

          }
        })
      }else{
        //
        this.$ajax({
        method: 'post',
        url: 'http://localhost:3000/api/getJudgeInfo',
        data: {
          sub_no: proIndex + 2,
          tf_no: index
        }
        })
        .then((response) => {
          console.log(response.data.tableData)
          response = response.data.tableData 
           if(index < 0){
            this.changeInfoInTable(response, 0)
          }else{
            this.judgeInfo = response[0]
            //
            this.showJudge = true
          }
        })
      }
    },
    changeInfoInTable(response, reference){
      //
      this.nowTable = response
      this.tableData = []
      for (var i = 0; i < response.length; i++){
        this.tableData.push({})
        if(reference){
          //
          this.tableData[i].code = response[i].cho_no
        }else{
          //
          this.tableData[i].code = response[i].tf_no
        } 
      }
      console.log(this.tableData)
    },
    clickList (index) {
      //
      this.index = index
    },
    deleteProInfo(proIndex, index){
      //
      if(proIndex){
        //
        this.$ajax({
        method: 'post',
        url: 'http://localhost:3000/api/deleteSelectInfo',
        data: {
          cho_no: this.nowTable[index].cho_no
        }
        })
        .then((response) => {
          this.loadProInfo(this.game1, this.testType, -1)
          
        })
      }else{
        //
        this.$ajax({
        method: 'post',
        url: 'http://localhost:3000/api/deleteJudgeInfo',
        data: {
          tf_no: this.nowTable[index].tf_no
        }
        })
        .then((response) => {
          this.loadProInfo(this.game1, this.testType, -1)
          
        })
      }
    },
    showProInfo(index){
      //
      this.loadProInfo(this.game1, this.testType, this.tableData[index].code)
    }

  }
}
</script>

<style lang="css">
.demo-table-settings{
  width: 200px;
  overflow: hidden;
  margin: 20px auto 0px;
}
</style>


